<template>
  <div class="main-div">
    <!-- <div class="h-35 d-inline-block" style="width: 100%">
      <h5 class="align-left pad-left">Loan Transaction details - employee:{{this.empId}}</h5>
    </div> -->
    <!-- {{this.totalRows}} -->
    <div class="h-35 d-inline-block" style="width: 100%">
        <h5 class="align-left pad-left">Transaction Details</h5>
      </div>
    <b-row>
      <div class="center">
        <b-container fluid>
          <b-table
           :sticky-header=true
           class="table table-bordered table-sm"
            selected-variant="active"
            :items="loanTransactions"
            :fields="fields"
            :current-page="currentPage"
            :filter="filter"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            :hover="true"
            v-model="visibleRows"
          >
            <template v-slot:cell(selected)="{ rowSelected }">
              <template v-if="rowSelected">
                <span class="sr-only">Selected</span>
              </template>
              <template v-else>
                <span class="sr-only">Not selected</span>
              </template>
            </template>
            <template slot="bottom-row" class="row-background">
              <td class="col-left">
                <h4><b-badge>Total</b-badge></h4>
              </td>
              <td></td>
              <td></td>
              <td></td>
              <td class="text-right">{{ t1.toFixed(3) | formatter1 }}</td>
            </template>
            <template slot="bottom-row">
              <td class="text-right">{{ t2.toFixed(3) | formatter1 }}</td>
            </template>
            <template slot="bottom-row">
              <td class="text-right">{{ t3.toFixed(3) | formatter1 }}</td>
            </template>
          </b-table>
          <!-- <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            size="sm"
            class="my-0"
          ></b-pagination> -->
        </b-container>
      </div>
    </b-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import moment from 'moment'

export default {
  props: {
    empId: {}
  },
  data () {
    return {
      visibleRows: [],
      currentPage: 1,
      perPage: 5,
      totalRows: 0,
      pageOptions: [5, 10, 15],
      sortBy: null,
      sortDesc: false,
      // sortDirection: "asc",
      filter: '',
      sortDirection: 'asc',
      loanTransactions: [],
      fields: [
        {
          key: 'id',
          label: 'Transaction Id',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'trnDate',
          label: 'Transaction Date',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'trnDescription',
          label: 'Description',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'voucherNo',
          label: 'Voucher No',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },

        {
          key: 'additionalInstallment',
          label: 'Additional Installment',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
        },
        {
          key: 'drAmount',
          label: 'Loan amount',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
        },
        {
          key: 'crAmount',
          label: 'Repaid amount',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
          // formatter (value) {
          //   return moment(value).format('DD-MM-YYYY')
          // }
        }
      ]
    }
  },
  mounted () {
    this.fetchLoanTransactions(this.empId)
  },
  components: {
  },
  computed: {
    ...mapGetters([
      'message',
      'employeeList',
      'fetchStartRec',
      'employee'
    ]),
    t1 () {
      return this.visibleRows.reduce((accum, item) => {
        return accum + parseFloat(item.additionalInstallment)
      }, 0.0)
    },
    t2 () {
      return this.visibleRows.reduce((accum, item) => {
        return accum + parseFloat(item.drAmount)
      }, 0.0)
    },
    t3 () {
      return this.visibleRows.reduce((accum, item) => {
        return accum + parseFloat(item.crAmount)
      }, 0.0)
    }
  },
  filters: {
    formatter1: function (value) {
      return new Intl.NumberFormat(undefined, {
        minimumFractionDigits: 2
      }).format(value)
    }
  },
  methods: {
    ...mapActions(['defaultState']),
    fetchLoanTransactions (x) {
      const url =
        process.env.VUE_APP_API_URL + `/employee/loantransactions/${x}`
      // console.log('url:' + url)
      fetch(url)
        .then(res => res.json())
        .then(data => {
          // console.log('Output: ', data)
          this.loanTransactions = data
          this.totalRows = this.loanTransactions.length
        })
        .catch(err => console.error(err))
    },
    formatData (value, x) {
      if (!value) return ''
      if (x.includes('date')) {
        // console.log('col name includes date')
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else if (x.includes('Date')) {
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else {
        // console.log('not formated')
        return value
      }
    }
  },
  watch: {
    empId: {
      handler: function () {
        this.fetchLoanTransactions(this.empId)
      }
    }
  }
}
</script>

<style scoped>
.main-div {
  width: 100%;
  margin-left: 5px;
  margin-right: 25px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: medium;
}
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}

#b-col {
  text-align: left;
}
.col-middle {
  /* vertical-align: bottom; */
  margin-left: -90px;
  padding-top: 25px;
  padding-left: 0px;
  -webkit-appearance: none;
}
.textBoxWidth {
  width: 160px;
}
.cellColor {
  background-color: #d9dcdf;
}
.calculated-value {
  background-color: #d9dcdf;
}
.input-required {
  background-color: rgb(245, 242, 212);
}
.page-head {
  font-weight: bold;
}
table, th, td {
  border-collapse: collapse;
}
th, td {
  padding: 25px;
}
tr:nth-child(even) {
  background-color: rgba(148, 150, 150, 0.4);
}

.center {
  margin: auto;
  width: 100%;
  padding: 10px;
}
/* th:nth-child(even),td:nth-child(even) {
  background-color: rgba(150, 212, 212, 0.4);
} */
</style>
