<template>
  <div class="main-div">
    <div id="showformid" v-show="showFormLoanTransaction">
      <loanTransaction />
    </div>
    <div id="showformlistid" v-show="showFormLoanSummaryList">
      <br />
      <b-row>
        <b-col cols="2">
          <!-- <button class="button" @click="addLoanTransaction()">
            Add Loan transaction
          </button> -->
          <b-button variant="secondary" @click="addLoanTransaction()"> Add Loan transaction</b-button>
        </b-col>
        <div class="center">
          <h5 class="center">Loan Summary</h5>
        </div>
      </b-row>
      <hr />
      <!-- <b-col cols="2">
            <button class="button" @click="addLoanTransaction()"> Add Loan transaction </button>
      </b-col>
      <div class="h-35 d-inline-block" style="width: 100%">
        <h5 class="align-left pad-left">Loan Summary</h5>
      </div>
      <hr /> -->
      <b-col cols="11" offset="1">
        <b-row>
          <div>
            <b-container fluid>
              <!-- User Interface controls -->
              <b-row>
                <b-col cols="6" class="my-1">
                  <b-form-group
                    label="Filter"
                    label-for="filter-input"
                    label-cols-sm="1"
                    label-align-sm="right"
                    label-size="sm"
                    class="mb-0"
                  >
                    <b-input-group size="sm">
                      <b-form-input
                        id="filter-input"
                        v-model="filter"
                        type="search"
                        placeholder="Type to Search"
                      ></b-form-input>

                      <b-input-group-append>
                        <b-button :disabled="!filter" @click="filter = ''"
                          >Clear</b-button
                        >
                      </b-input-group-append>
                    </b-input-group>
                  </b-form-group>
                </b-col>

                <b-col cols="6" class="my-1">
                  <b-form-group
                    v-model="sortDirection"
                    label="Filter On"
                    description="Leave all unchecked to filter on all data"
                    label-cols-sm="3"
                    label-align-sm="right"
                    label-size="sm"
                    class="mb-0"
                    v-slot="{ ariaDescribedby }"
                  >
                    <b-form-checkbox-group
                      v-model="filterOn"
                      :aria-describedby="ariaDescribedby"
                      class="mt-1"
                    >
                      <b-form-checkbox value="empCode"
                        >Employee Code</b-form-checkbox
                      >
                      <b-form-checkbox value="empName"
                        >Employee Name</b-form-checkbox
                      >
                      <!-- <b-form-checkbox value="isActive">Active</b-form-checkbox> -->
                    </b-form-checkbox-group>
                  </b-form-group>
                </b-col>
              </b-row>
              <b-row>
                <b-col cols="5" class="my-1">
                  <b-form-group
                    label="Per page"
                    label-for="per-page-select"
                    label-cols-sm="2"
                    label-align-sm="left"
                    label-size="sm"
                    class="mb-0"
                  >
                    <b-form-select
                      id="per-page-select"
                      v-model="perPage"
                      :options="pageOptions"
                      size="sm"
                    ></b-form-select>
                  </b-form-group>
                </b-col>
                <b-col sm="7" md="6" class="my-1">
                  <b-pagination
                    v-model="currentPage"
                    :total-rows="rowTotal"
                    :per-page="perPage"
                    align="fill"
                    size="sm"
                    class="my-0"
                  ></b-pagination>
                </b-col>
                <b-col>
                  <b-button
                    variant="outline-primary"
                    size="sm"
                    class="float-right"
                    @click="refreshData()"
                  >
                    Refresh
                  </b-button>
                </b-col>
              </b-row>
              <b-table
                striped
                hover
                :items="loanSummaryList"
                :fields="fields"
                :current-page="currentPage"
                :per-page="perPage"
                :filter="filter"
                :filter-included-fields="filterOn"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :sort-direction="sortDirection"
                stacked="md"
                show-empty
                small
                @filtered="onFiltered"
              >
                <template #table-colgroup="scope">
                  <col
                    v-for="field in scope.fields"
                    :key="field.key"
                    :style="{
                      width: field.key === 'empName' ? '400px' : '150px',
                    }"
                  />
                </template>
                <template #cell(name)="row">
                  {{ row.value.first }} {{ row.value.last }}
                </template>
                <template #cell(actions)="row">
                  <b-button size="sm" @click="showDetails(row.item)">
                    {{ row.detailsShowing ? "Hide" : "Show" }} Details
                  </b-button>
                </template>
                <template #row-details="row">
                  <b-card>
                    <ul>
                      <li v-for="(value, key) in row.item" :key="key">
                        {{ key }}: {{ value }}
                      </li>
                    </ul>
                  </b-card>
                </template>
              </b-table>
            </b-container>
          </div>
        </b-row>
      </b-col>
    </div>
    <hr />
    <!-- <div id="showformid" v-show="showLoanTransactions" class="main-div">
        <loanDetail :empId="this.empId" />
    </div> -->
    <b-modal ref="my-modal" size="xl">
      <loanDetail :empId="this.empId" />
    </b-modal>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import moment from 'moment'
import loanTransaction from './LoanTransaction.vue'
import loanDetail from './LoanDetail.vue'
export default {
  data () {
    return {
      showLoanTransactions: false,
      empId: {},
      visibleRows: [],
      loanSummaryList: [],
      rowTotal: 0,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15],
      sortBy: null,
      sortDesc: false,
      filter: '',
      filterOn: [],
      sortDirection: 'asc',
      leaveHistoryList: [],
      fields: [
        {
          key: 'id',
          label: 'Code',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'empName',
          label: 'Name',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'lastTransactionDate',
          label: 'Last transaction date',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
        },

        {
          key: 'totalLoans',
          label: 'Total Loans',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
        },
        {
          key: 'totalPayment',
          label: 'Total Repaid',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
          // formatter (value) {
          //   return moment(value).format('DD-MM-YYYY')
          // }
        },
        { key: 'actions', label: 'Actions' }
      ]
    }
  },
  mounted () {
    this.fetchLoanSummaryList()
  },
  components: {
    loanDetail,
    loanTransaction
    // VueNumeric
  },
  computed: {
    ...mapGetters([
      'message',
      'employee',
      'showFormLoanSummaryList',
      'showFormLoanTransaction'
    ]),
    sortOptions () {
      // Create an options list from our fields
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => {
          return { text: f.label, value: f.key }
        })
    }
  },
  filters: {
    formatter1: function (value) {
      return new Intl.NumberFormat(undefined, {
        minimumFractionDigits: 2
      }).format(value)
    }
  },
  methods: {
    ...mapActions(['setLoanFormDisplay', 'defaultState']),
    async fetchLoanSummaryList () {
      const url = process.env.VUE_APP_API_URL + '/loanSummary'
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          this.loanSummaryList = data
          this.rowTotal = this.loanSummaryList.length
          console.log(this.loanSummaryList)
        })
        .catch((err) => console.error(err))
    },
    refreshData () {
      alert('refresh data')
      this.fetchLoanSummaryList()
    },
    formatData (value, x) {
      if (!value) return ''
      if (x.includes('date')) {
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else if (x.includes('Date')) {
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else {
        return value
      }
    },
    onFiltered (filteredItems) {
      this.rowTotal = filteredItems.length
      this.currentPage = 1
    },
    showDetails (x) {
      this.empId = x.id
      this.$refs['my-modal'].show()
      // this.showLoanTransactions = !this.showLoanTransactions
    },
    addLoanTransaction: function () {
      this.defaultState()
      this.setLoanFormDisplay()
    }
  }
}
</script>

<style>
.main-div {
  width: 95%;
  margin-left: 20px;
  margin-right: 10px;
  font-size: medium;
}
.table-div {
  width: 90%;
  margin-left: 40px;
  margin-right: 0px;
  font-size: small;
}
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}
.input-required {
  background-color: rgb(245, 242, 212);
}

#b-col {
  text-align: left;
}

.textBoxWidth {
  width: 160px;
}
.cellColor {
  background-color: #d9dcdf;
}
.calculated-value {
  background-color: #d9dcdf;
}
.sub-head {
  background-color: #d9dcdf;
}
.group1-color {
  background-color: rgb(242, 242, 242);
}
.group2-color {
  background-color: rgb(230, 230, 230);
}
p {
  margin-bottom: 0px;
}
.spacer {
  margin-top: 5px;
  margin-bottom: 5px;
}
.center {
  margin-left: auto;
  margin-right: auto;
}
</style>
